import React, { useState } from 'react';
import "./MakeRoom.scss";
import RadioButton from '../button/RadioButton';
import Button from '../button/Button';
import axios from 'axios';
import cookie from 'react-cookies';

const MakeRoom = ({click, change}) => {
    //   
    const clickModalFrameHandler = (event) =>{        
        if(event.target.className === 'modal-frame'){            
            click();
        }        
    }
    //  1 , 2 
    const [clicked , setClicked] = useState(false); 
    const clickRadioButtonHandler = () =>{
        setClicked(!clicked);
    } 
    const [isClear, setIsClear] = useState(false);
    const postUserInfo = () =>{
        axios.post("http://localhost:8181/create-room")
        .then(res => { //    
            cookie.save("userId", res.data, { path: '/' });  // <-    
            console.log("  : " + res.data);
            return true; //    
        })
        .then((isClear) => {
            setIsClear(isClear);
            click();
            change(2);
        });
    }

return (
    <div className='modal-frame' onClick={clickModalFrameHandler}> 
            <div className='__body mount'>                                    
                    <span className='title'> </span>
                    <div className='horizontal'/>
                    <span className='rule-title'></span>
                    <div className='set-rule'>
                        <RadioButton disabled={false} children="1" onClickEvent = {clickRadioButtonHandler} checked={!clicked}/>                            
                        <RadioButton disabled={false} children="2" onClickEvent = {clickRadioButtonHandler} checked={clicked}/>                            
                    </div>
                    <Button title=" " style="button5" event={()=>{
                        postUserInfo();
                    }} />
                </div>            
        </div>
  )
}

export default MakeRoom;