import React, {useState} from 'react';
import './Main.scss';
import Button from '../components/button/Button';
import Title from '../components/title/Title';
import RuleModal from '../components/modal/RuleModal';
import  ReactDOM  from 'react-dom';
import MakeRoom from '../components/modal/MakeRoom';
import FindModal from '../components/modal/FindModal';

const Main = ({change}) => {
  const [start, setStart] = useState(false);
  const [make, setMake] = useState(false);
  const [find, setFind] = useState(false);
  const [isOpenHowButton, setIsOpenHowButton] = useState(false);  
  //      
  const clickStartButtonHandler = () =>{
    setStart(!start);    
  }
  const clickHowButtonHandler = () =>{
    setIsOpenHowButton(!isOpenHowButton);
  }
  const clickMakeRoomButtonHandler = () =>{    
    setMake(!make);
    console.log("  ");
  }
  const clickFindRoomButtonHandler = () =>{
    setFind(!find);
    console.log("   ");
  }  

  //   
  if(!start){
    return (
      <div className='main'>
        <Title title=" "/>
        <Button title=" " style ="button1" event = {clickStartButtonHandler}/>
        <Button title=" " style ="button2" event = {clickHowButtonHandler}/>                  
        {
          isOpenHowButton && (
            ReactDOM.createPortal(<RuleModal click={clickHowButtonHandler}/>, document.getElementById('overlay-root'))
          )
        }      
      </div>      
    )
  }
  //       
  if(start){
    return(
      <div className='main'>
        <Title title=" "/>
        <Button title=" " style="button3" event = {clickMakeRoomButtonHandler}/>
        <Button title='  ' style="button4" event = {clickFindRoomButtonHandler}/>
        {
          make && (
            ReactDOM.createPortal(<MakeRoom click={clickMakeRoomButtonHandler} change={change}/>, document.getElementById('overlay-root'))
          )
        }
        {
          find && (
            ReactDOM.createPortal(<FindModal click={clickFindRoomButtonHandler} change={change} />, document.getElementById('overlay-root'))
          )
        }     
      </div>
    )
    }
  }
  




export default Main